<template>
	<div id="blog-page">
		<div class="blog container">
			<h3 class="text-center">Blog</h3>

			<div class="blog-wrapper mt-5">
				<ul class="post-list">
					<li v-for="(p) in post" :key="p.id">
						<router-link :to="{ name: 'BlogPost', params: { slug: p.slug } }">{{p.title}}</router-link>
					</li>
				</ul>
			</div>
		</div>
		<p class="text-center my-4">
			<small>Created By Putu Jhonarendra. Pageviews: {{pageview.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}}</small>
		</p>
		<ButtonToTop />
		<div class="pb-5"></div>
	</div>
</template>

<script>
import firebase from './../services/firebase'
// import axios from 'axios'
import viewCounter from './../mixins/viewCounter'
import ButtonToTop from '../components/ButtonToTop'
import '../assets/css/blog.css'
export default {
	data(){
		return {
			post: []
		}
	},
	mixins: [viewCounter],
	components: {
		ButtonToTop
	},
	methods: {
		getSitemap(){
			const db = firebase.firestore()
			db.collection('blog')
			.get()
			.then((q) => {
				let data = []
				q.forEach((doc) => {
					data.push(doc.data())
				})
				data.sort((a, b)=>{
					let dateA = new Date(a.datetime.toDate().toString())
					let dateB = new Date(b.datetime.toDate().toString())
					return dateB - dateA
				})
				this.post = data
			})
			.catch((error) => {
				console.error("Error getting documents: ", error)
			})
		}
	},
	mounted(){
		this.updatePageview() // call mixins
		this.getSitemap()
	}
}
</script>